<template>
	<view>    
		<cmpA v-if="child=='a'" :data="data.data" :fun="fatherfun" />
		<cmpB v-if="child=='b'" :data="data.data" :fun="fatherfun" />
	</view>
</template>

<script setup>  
import cmpA from './a.vue'
import cmpB from './b.vue' 
import {jumpUtil} from "@/utils/common.js"
const props = defineProps({
  data: {
    type:[],
    required: true
  },
  child: {
    type: String,
    required: true
  }
})
const fatherfun=(e)=>{
	if(e.jump_class && e.jump_id){
		jump(e.jump_class,e.jump_id)
	}
	 
}
const jump=(type,id)=>{
	jumpUtil(type,id);
}
</script>
 