<template>
	<view>
		<cmpA v-if="child=='a'" :cpy="cpy" />
		<cmpB v-if="child=='b'" :cpy="cpy" />
	</view>
</template>

<script setup>
	import cmpA from './a.vue'
	import cmpB from './b.vue'
	import {useBeforeStore} from "@/stores/beforeStore.js"
	const beforeStore = useBeforeStore()
	const cpy = ref()
	onMounted(() => {
		beforeStore.getSys.then(res=>{ 
				cpy.value =res
		}) 
	})
	const props = defineProps({
		data: {
			type: [],
			required: true
		},
		child: {
			type: String,
			required: true
		}
	})
	
	
</script>
