<template>
	<view class="fixed flex justify-evenly bottom-0 h-14 bg-white w-full overflow-hidden
border-t-gray" v-if="index>-1">

		<block v-for="(item, i) in list" :key="i">
			<div class="mt-2 text-gray-400" @click="jump(i)">
				<div class="h-6 w-6 m-auto svg">
					<img :src="iconObj[item.icon.toLowerCase()]" 
					:class="i == index ? 'svg-color' : 'svg-icon'" />
				</div>
				<div class="mt-1 text-sm" :class="i == index ? 'text-blue-400' : ''" >
				{{ item.name }}</div>
			</div>
		</block>
	</view>
</template>

<script setup>
	import {
		useNavStore
	} from '@/stores/navStore.js';
	import {
		onLoad
	} from '@dcloudio/uni-app';

	import { icons } from '@/utils/icon.js';	
	const iconObj = icons()  

	const route = useRoute();
	const navStore = useNavStore();
	const index = ref();
	const list = ref();

	onLoad(options => {
		if (options.id < 1) {
			return;
		}
		const id = options.id;
		const pages = getCurrentPages();
		const page = pages[pages.length - 1];
		let path ="/"
		// #ifdef MP-WEIXIN
		 path = '/' + page.route;
		// #endif

		// #ifndef  MP-WEIXIN
		if (process.env.NODE_ENV === 'development') {			
			path = page.route.path;
		} else {
		  path = page.route;
		}
		// #endif 		
		navStore.getData.then(res => { 
			list.value = res;
			index.value = getIndex(path, id); 
			navStore.setIndex(index.value);
		});
	});

	const getIndex = (path, id) => { 
		for (let i = 0; i < list.value.length; i++) {
			const item = list.value[i];
			let oldPath='pages/'+item.jump_type+'/'+item.jump_type 
			if(path){
				path=path.replace('/pages','pages') 
			}
			if (oldPath == path && item.jump_id == id) {
				return i;
			}
			if(!path || path=='pages/index/index' || path=='pages/home/home'){
				return 0
			}
		}
		return -1;
	};

	const jump = i => { 
		const item = list.value[i];
		let url="/"
		// #ifdef MP-WEIXIN
		 url="/pages/index/index"
		// #endif 	
		if (item.jump_type !== 'home') {
			url='/pages/'+item.jump_type+'/'+item.jump_type + '?id=' + item.jump_id			
		}
		uni.reLaunch({
			url
		});
	};
</script>

<style lang="scss">
	.svg {
		width: 24px;
		height: 24px;
		overflow: hidden;
			.svg-color {
				width: 24px;
				height: 24px;
				transform: translateX(24px);
				filter: drop-shadow(#409EFF -24px 0px 0px);
			} 
			.svg-icon {
				width: 24px;
				height: 24px;
				transform: translateX(24px);
				filter: drop-shadow(#909399 -24px 0px 0px);
			} 
	}
</style>
