<template>
	<view>
		<cmpA v-if="child=='a'" :data="data.data" :fun="fatherfun" />
		<cmpB v-if="child=='b'" :data="data.data" :fun="fatherfun" />
		<cmpC v-if="child=='c'" :data="data.data" :fun="fatherfun" />
		<cmpD v-if="child=='d'" :data="data.data" :fun="fatherfun" />
		<cmpE v-if="child=='e'" :data="data.data" :fun="fatherfun" />
	</view>
</template>

<script setup>
import cmpA from './a.vue'
import cmpB from './b.vue'
import cmpC from './c.vue'
import cmpD from './d.vue'
import cmpE from './e.vue'
const props = defineProps({
	data: {
		type: [],
		required: true
	},
	child: {
		type: String,
		required: true
	}
});
const fatherfun=(options)=>{
	if(options.id && options.cid){
		jump(options.cid,options.id)
	}
}
const jump=(cid,id)=>{
		uni.navigateTo({
			url: '/pages/detail/detail?cid='+cid+'&id='+id
		});
}
</script>
