import {defineStore} from 'pinia';
import {apiFootNavs} from '@/api/category.js';

export const useNavStore = defineStore('nav', {
	state: () => ({
		data: [],
		index:0
	}),
	getters: {
		async getData() {
			if(this.data.length==0){
				await this.setData()
			}
			return this.data
		},
		getIndex(){			 
			return this.index
		}
	},
	actions: {
		setData() { 
			return apiFootNavs().then(res => {  
				for(let i=0;i<res.length;i++){
					if(res[i].jump_type=='home'){
						res[i].path='/pages/index/index'
					}
					if(res[i].jump_type=='cover'){
						res[i].path='/pages/cover/cover'
					}
					if(res[i].jump_type=='list'){
						res[i].path='/pages/list/list'
					} 
				}
				this.data=res
				return res
			})
		},
		setIndex(index) {
			 this.index=index
		},
	}
});
