import {defineStore} from 'pinia'; 
import {apiSysData} from "@/api/index.js"

export const useBeforeStore = defineStore('before', {
	state: () => ({
		sys: {}
	}),
	getters: {
		async getSys() {
			if(Object.keys(this.sys).length==0){ 
				await this.setSys() 
			}
			return this.sys
		} 
	},
	actions: {
		setSys() { 
			return apiSysData().then(res=>{			 
				this.sys=res
 				return res
			})
		} 
	}
});
