<template>
	<div class="bg-white text-gray-500 text-sm">
		<block v-for="(item, index) in data" :key="index">
			<div @click="show(index)"  class="border-b-gray px-4 py-3 flex justify-between">
				<div>{{ item.ask }}</div>
				<div class="w-4 h-4 pt-1 text-gray-400"><!-- <el-icon><component is="ArrowDown" /></el-icon> --></div>
			</div>
			<div v-if="showArr[index]" class="border-gray px-4 py-3 border-b-1 text-gray-400">{{ item.answer }}</div>
		</block>
	</div>
</template>

<script setup>
const props = defineProps({
	data: {
		type: [],
		required: true
	}
});
const showArr = ref();
showArr.value = new Array(props.data.length).fill(false);
const show = index => {
	for (let i = 0; i < showArr.value.length; i++) {
		const item = showArr.value[i];
		if (i === index) {
			showArr.value[i] = !showArr.value[i];
		} else {
			showArr.value[i] = false;
		}
	}
	console.log(showArr.value);
};
</script>
