/**
 * 引入element-plus/icons-vue,并把所有图标循环注册为全局组件
 */

//小程序不支持全局引入组件
const importModules =
	import.meta.globEager('../static/svg/*.svg');


export function icons() {
	let obj = {}
	for (const [key, component] of Object.entries(importModules)) {
		const name = key.replace("../static/svg/", "").replace(".svg", "")
		obj[name] = component.default
	}
	return obj
}
